import Vue from 'vue'
import Router from 'vue-router'
import main from '@/components/main'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/', redirect: '/main'},
    { path: '/main', component: resolve => require(['@/components/main.vue'], resolve),
      children:[
        {path: '/', redirect: '/zhuye'},
        {path: '/zhuye', component: resolve => require(['@/components/zhuye/zhuye.vue'], resolve)},
        {path: '/me', component: resolve => require(['@/components/me/me.vue'], resolve)},
        {path: '/dongtai', component: resolve => require(['@/components/dongtai/dongtai.vue'], resolve)},
        {path: '/project', component: resolve => require(['@/components/project/project.vue'], resolve)},

      ]
    },
    {path: '*',component: resolve => require(['@/components/error404.vue'], resolve)},
  ]
})



