<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
body{
  width: 100%;
  height: 100%;
  background: url("./assets/body_repeat.png") repeat;
  background-size: cover;
  overflow-x: hidden;
  /*background-color: #fefefe;*/
}

 #app:before{
    position: absolute;
    display: block;
    width: 100%;
    z-index: -1;
    content: '';
    min-height: 500px;
    min-width: 1028px;
    filter: blur(1px);
    background: url("./assets/bg.jpg") no-repeat;
    background-size: 100% 500px;
    animation: bgmove 30s linear infinite;
  }
 @keyframes bgmove {
   0%{
     transform: translateX(-70px);
   }
   50%{
     transform: translateX(70px);
   }
   100%{
     transform: translateX(-70px);
   }
 }
.clear:after {
  display: block;
  content: '';
  clear: both;
}



/*css抛物线动画*/
.animtion{
  margin-top: 10px;
  width: 100px;
  height: 100px;
  border: solid #ddd;
  border-width: 2px 2px 0 0;
  background: linear-gradient(to top, #ddd 10%, transparent 10%), linear-gradient(to right, #ddd 10%, transparent 10%);
  background-size: 100% 20%, 20%, 100%;
  position: relative;

}
.anitem ,.anitem2{
  width:20px;
  height: 20px;
  top:0;
  left: 0;
  display: inline-block;
  position: absolute;
  background-color: #00aa00;
  border-radius: 50%;
}
.anwraper {
  animation: vertical-animation 2s  .5s infinite alternate;
  animation-timing-function: cubic-bezier(.11,-.55,.55,.11);
}
.anwraper .anitem {
  animation: hor-animation 2s linear .5s infinite alternate;
}
@-moz-keyframes hor-animation {
  0% { transform: translateX(0px); }
  100% { transform: translateX(100px); }
}
@-webkit-keyframes hor-animation {
  0% { transform: translateX(0px);     }
  100% { transform: translateX(100px); }
}
@-moz-keyframes vertical-animation {
  0% { transform: translateY(0px);  }
  100% { transform: translateY(100px); }
}
@-webkit-keyframes vertical-animation {
  0% { transform: translateY(0px);     }
  100% { transform: translateY(100px); }
}
/*----------------------------------------------------*/
</style>



